import { Link } from "react-router-dom";

import "./ItemLink.scss";

export interface ItemLinkProps {
  url: string;
  text: string;
  img: string;
}

function ItemLink({ text, img, url }: ItemLinkProps) {
  return (
    <Link to={url} className="item-link-component">
      <img alt={`Imagem de ${text}`} src={img} className="icone"></img>
      <h2 className="title">{text}</h2>
    </Link>
  );
}

export default ItemLink;
