import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import GrpcApi from "../../api/GrpcMainApi";
import ItemList from "../../components/item-list/ItemList";
import Item from "../../components/item-link/ItemLink";

interface ProdutoData {
  id: number;
  nome: string;
  icone: string;
}

interface ProdutoParams {
  id: string | undefined;
}

function Produtos() {
  const [produtos, setProdutos] = useState<ProdutoData[]>([]);
  const [loading, setLoading] = useState(true);
  const { id } = useParams<ProdutoParams>();

  useEffect(() => {
    const api = new GrpcApi();
    if (id) {
      const idParsed = parseInt(id);
      if (isNaN(idParsed)) {
        setLoading(false);
        return;
      }

      api.getProdutos(idParsed).then(async (response) => {
        const produtosPromises = await Promise.allSettled(
          response.map(async (produto) => {
            return {
              id: produto.getId(),
              nome: produto.getNome(),
              icone: await api.getFoto(produto.getIcone()),
            } as ProdutoData;
          })
        );

        setLoading(false);
        produtosPromises.forEach((promise) => {
          if (promise.status === "fulfilled") {
            setProdutos((produtos) => [...produtos, promise.value]);
          }
        });
      });
    }
  }, [id]);

  return (
    <ItemList isLoading={loading}>
      {produtos.map((produto) => {
        const { id: idProduto, nome, icone } = produto;
        return (
          <Item
            key={idProduto}
            img={icone}
            text={nome}
            url={`/categorias/${id}/produtos/${idProduto}`}
          />
        );
      })}
    </ItemList>
  );
}

export default Produtos;
