import React, { Children } from "react";
import "./ItemList.scss";

export interface ItemListProps {
  children: React.ReactNode;
  isLoading: boolean;
}

function ItemList({ children, isLoading }: ItemListProps): JSX.Element {
  const lenghtChildren = Children.count(children);
  if (isLoading) {
    return (
      <div className="item-list-component center">
        <h1>Carregando...</h1>
      </div>
    );
  } else if (lenghtChildren === 0) {
    return <NoItens />;
  } else {
    return <div className="item-list-component">{children}</div>;
  }
}

function NoItens() {
  return (
    <div className="item-list-component center">
      <h1>Sem itens para ser mostrado</h1>
    </div>
  );
}

export default ItemList;
