import { Route, BrowserRouter, Switch } from "react-router-dom";

import Home from "./views/index/Index";
import Categorias from "./views/main-frame/MainFrame";

function Routes() {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/" exact>
          <Home />
        </Route>
        <Route path="/categorias">
          <Categorias />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}

export default Routes;
