import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";

import Api from "../../api/GrpcMainApi";
import Slide from "../../components/slide/Slide";
import WppLogo from "../../assets/svgs/wpp.svg";

import "./Produto.scss";

interface ProdutoParams {
  produto: string | undefined;
}

interface ProdutoData {
  nome: string;
  descricao: string;
  imagens: number[];
}

function Produto() {
  const { produto: id } = useParams<ProdutoParams>();
  const [produto, setProduto] = useState<ProdutoData | null>(null);
  const [loading, setLoading] = useState(true);
  const [imageLoaded, setImageLoaded] = useState<string[]>([]);

  useEffect(() => {
    const api = new Api();
    if (id) {
      const idNum = parseInt(id);
      if (!isNaN(idNum)) {
        api
          .getProduto(idNum)
          .then(async (produtoResponse) => {
            setProduto({
              nome: produtoResponse.getNome(),
              descricao: produtoResponse.getDescricao(),
              imagens: produtoResponse.getFotosList(),
            });

            const images = produtoResponse.getFotosList().map((img) => {
              return api.getFoto(img);
            });

            const imgFullyLoad = await Promise.all(images);
            setImageLoaded(imgFullyLoad);
            setLoading(false);
          })
          .catch(console.error);
      }
    }
  }, [id]);

  if (loading) {
    return (
      <div className="produto-page center">
        <h2>Carregando...</h2>
      </div>
    );
  }

  if (!produto) {
    return (
      <div className="produto-page center">
        <h2>Produto não existe!</h2>
      </div>
    );
  }

  return (
    <div className="produto-page">
      <h1>{produto.nome}</h1>
      <Slide className="config-carrousel" images={imageLoaded} />
      <h2>Informações:</h2>
      <p>{produto.descricao}</p>
      <a href={whatsAppLink(produto.nome)} className="whatsapp">
        <img src={WppLogo} alt="Whatsapp Logo" />
        <span>Mais Informações</span>
      </a>
    </div>
  );
}

function whatsAppLink(nome: string) {
  const now = new Date().getHours();

  let comeco = "Boa noite";
  if (now >= 5 && now < 12) {
    comeco = "Bom dia";
  } else if (now >= 12 && now < 18) {
    comeco = "Boa tarde";
  }

  return (
    "https://wa.me/5511995401715/?text=" +
    encodeURI(`${comeco}, gostaria de mais informações sobre a(o) ${nome}`)
  );
}

export default Produto;
