import { useState, useEffect } from "react";
import { useLocation } from "react-router";
import GrpcApi from "../../api/GrpcMainApi";
import { Tipo } from "../../api/data-service_pb";
import ItemLink from "../../components/item-link/ItemLink";
import ItemList from "../../components/item-list/ItemList";

interface CategoriaData {
  id: number;
  nome: string;
  icone: string;
}

function Categorias() {
  const query = new URLSearchParams(useLocation().search);
  const categoria_tipo = query.get("tipo");

  const [categorias, setCategorias] = useState<CategoriaData[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    let tipo_final: Tipo;

    switch (categoria_tipo) {
      case "decoracoes":
        tipo_final = Tipo.DECORACAO;
        break;
      case "artes":
        tipo_final = Tipo.ARTE;
        break;
      default:
        tipo_final = Tipo.MOVEIS;
    }

    const api = new GrpcApi();
    api.getCategorias(tipo_final).then((categorias) => {
      Promise.allSettled(
        categorias.map(async (categoria) => {
          return {
            id: categoria.getId(),
            nome: categoria.getNome(),
            icone: await api.getFoto(categoria.getIcone()),
          } as CategoriaData;
        })
      ).then((promises) => {
        setLoading(false);
        promises.forEach((promise) => {
          if (promise.status === "fulfilled") {
            setCategorias((categorias) => [...categorias, promise.value]);
          }
        });
      });
    });
  }, [categoria_tipo]);

  return (
    <ItemList isLoading={loading}>
      {categorias?.map((categoria) => {
        const { id, nome, icone } = categoria;
        return (
          <ItemLink
            key={id}
            img={icone}
            text={nome}
            url={`/categorias/${id}`}
          />
        );
      })}
    </ItemList>
  );
}

export default Categorias;
