import { ProdutosDataClient } from "./Data-serviceServiceClientPb";
import {
  Categoria,
  Tipo,
  FilterTipo,
  FotoID,
  Produto,
  FilterProduto,
  ProdutoID,
} from "./data-service_pb";

export default class GrpcMainApi {
  private grpcProdutosClient: ProdutosDataClient;
  constructor() {
    this.grpcProdutosClient = new ProdutosDataClient(
      "https://api.madeiranobremoveis.com.br"
    );
  }

  public async getCategorias(tipoCategoria: Tipo): Promise<Categoria[]> {
    const filter = new FilterTipo();
    filter.setTipo(tipoCategoria);
    const response = await this.grpcProdutosClient.getCategorias(filter, null);
    return response.getCategoriasList();
  }

  public async getFoto(id: number): Promise<string> {
    const fotoID = new FotoID();
    fotoID.setId(id);
    const response = await this.grpcProdutosClient.getFoto(fotoID, null);
    const blob = new Blob([response.getFoto_asU8()], { type: "image/*" });
    return URL.createObjectURL(blob);
  }

  public async getProdutos(idCategoria: number): Promise<Produto[]> {
    const filterProduto = new FilterProduto();
    filterProduto.setIdcategoria(idCategoria);
    const response = await this.grpcProdutosClient.getProdutos(
      filterProduto,
      null
    );
    return response.getProdutosList();
  }

  public async getProduto(id: number): Promise<Produto> {
    const produtoID = new ProdutoID();
    produtoID.setId(id);
    const response = await this.grpcProdutosClient.getProduto(produtoID, null);
    return response;
  }
}
