import { useRef, useEffect } from "react";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import "@splidejs/splide/dist/css/themes/splide-default.min.css";
import "./Slide.scss";

function Slide({ images, className }) {
  const mainSplide = useRef(null);
  const subSplide = useRef(null);

  useEffect(() => {
    mainSplide.current.sync(subSplide.current.splide);
  }, []);

  return (
    <div className={`slide-component ${className}`}>
      <Splide
        ref={mainSplide}
        className="main"
        options={{
          type: "loop",
          perPage: 1,
          perMove: 1,
          gap: "1rem",
          pagination: false,
        }}
      >
        {images.map((image, index) => (
          <SplideSlide key={index}>
            <img src={image} alt={`${index + 1} imagem`} />
          </SplideSlide>
        ))}
      </Splide>
      <Splide
        ref={subSplide}
        className="slave link"
        options={{
          type: "slide",
          rewind: true,
          gap: ".5rem",
          pagination: false,
          fixedWidth: 70,
          fixedHeight: 70,
          arrows: false,
          cover: true,
          focus: "center",
          isNavigation: true,
          updateOnMove: true,
        }}
      >
        {images.map((image, index) => (
          <SplideSlide key={index}>
            <img src={image} alt={`${index + 1} imagem`} />
          </SplideSlide>
        ))}
      </Splide>
    </div>
  );
}

export default Slide;
